/************newslist start*****************/
.content_margin {
  margin-top: 1px;
  margin-bottom: 50px;
}
/************newslist end*****************/
/************toplistview覆盖默认样式 start*****************/
.toplist .am-list-item {
  background-color: red;
}
/************toplistview覆盖默认样式 end*****************/
/************newsdetail start*****************/
.aui-flex {
  display: flex;
  align-items: center;
  padding: 0 15px;
}
.aui-flex-box {
  flex: 1 1;
  min-width: 0;
  font-size: 14px;
  color: #333;
}
.aui-flex-box h2 {
  text-align: center;
  font-size: 1rem;
  color: #333;
}
.aui-flex-box h4 {
  text-align: center;
  font-size: 0.5rem;
  color: #333;
}
.aui-flex-box p {
  color: #666666;
  font-size: 0.85rem;
  margin-bottom: 0.35rem;
}
/************newsdetail end*****************/
/************newsdetail sharebutton start*****************/
.share_button {
  position: fixed !important;
  bottom: 10px;
  left: 5px;
  right: 5px;
  margin-left: auto;
  margin-right: auto;
}
/************newsdetail sharebutton end*****************/
/************MyInfoListView item start*****************/
.aui-tags-emergency {
  font-size: 1rem;
  color: red;
  padding: 2px 3px;
}
.aui-tags-normal {
  font-size: 1rem;
  color: green;
  padding: 2px 3px;
}
/************MyInfoListView item end*****************/
/************info submit button start*****************/
.info-submit-button {
  margin: 0 5px;
}
/************info submit button end*****************/
